import { setLoginInfo, getLoginInfo, removeLoginInfo } from '@/utils/data.js';

export default {
	data() {
		return {
			autoLoading: true,
			loading: false,
			form: {
				account: '',
				password: ''
			}
		};
	},

	created() {
		const loginInfo = getLoginInfo();
		if (loginInfo && loginInfo.account && loginInfo.password) {
			this.form.account = loginInfo.account;
			this.form.password = loginInfo.password;
			this.login();
		} else {
			this.autoLoading = false;
		}
	},

	methods: {
		// 登录
		async login() {
			if (!this.form.account || !this.form.password) {
				this.$alert('用户名或密码为空！');
				return;
			}
			try {
				this.loading = true;
				const res = await this.$request({
					url: '/auth/login',
					method: 'POST',
					params: {
						account: this.form.account,
						password: this.form.password
					}
				});
				this.loading = false;
				setLoginInfo({
					...this.form,
					auid: res.auid,
					access_token: res.access_token
				});
				uni.reLaunch({ url: '/pages/tabbar/index' });
			} catch (e) {
				//TODO handle the exception
				this.autoLoading = false;
				this.loading = false;
				this.$alert('用户名或密码错误！');
			}
		}
	}
};
